@import '../../style/button.scss';
@import '../../style/_variables.scss';

.comics {
    &__list {
        margin-top: 45px;
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(auto-fill, 225px);
        justify-content: space-between;
        row-gap: 55px;
    }

    &__item {
        transition: 0.3s transform;

        &-img {
            box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
            width: 225px;
            height: 345px;
            object-fit: cover;
        }

        &-name {
            margin-top: 10px;
            font-weight: bold;
            font-size: 14px;
            line-height: 18px;
        }

        &-price {
            margin-top: 5px;
            font-weight: bold;
            font-size: 14px;
            line-height: 18px;
            color: rgba(0, 0, 0, 0.6);
        }

        &:hover {
            transform: translateY(-5px);
        }
    }
}

.alert-enter {
    opacity: 0;
    transform: scale(0.5);
}

.alert-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 300ms, transform 300ms;
}